import React from "react";
import { useParams } from "react-router-dom";
import config from "../config";
import axios from "axios";
import { Title } from "react-head-meta";
import { Center, Box, Heading } from "@chakra-ui/react";
import Loader from "../components/Loader";

function Watch() {
	let { filmId } = useParams();
	const [notFound, setNotFound] = React.useState<boolean>(false);
	const [title, setTitle] = React.useState<string>("");
	const [isLoading, setIsLoading] = React.useState<boolean>(true);
	React.useEffect(() => {
		const check = async () => {
			setNotFound(false);
			setIsLoading(true);
			try {
				const res = await axios.get(`${config.api_url}/films/${filmId}`, {
					headers: {
						"X-API-KEY": config.api_token,
					},
				});

				if (res.status === 200) {
					setTitle(res.data.nameRu || res.data.nameEn);
					setNotFound(false);
					const script = document.createElement("script");
					script.src = "/js/watch.js";
					document.body.appendChild(script);
				} else {
					setNotFound(true);
				}
			} catch (e) {
				setNotFound(true);
			} finally {
				setIsLoading(false);
			}
		};
		check();
	}, [filmId]);
	return (
		<>
			{(isLoading && (
				<>
					<Title title="KinoSearch" />
					<Loader />
				</>
			)) || (
				<>
					{(notFound && (
						<Center h="80vh">
							<Title title="Страница не найдена – KinoSearch" />
							<Heading>Страница не найдена</Heading>
						</Center>
					)) || (
						<>
							<Title title="Просмотр фильма – KinoSearch" />
							<Center paddingBottom={10}>
								<Heading size="xl" fontWeight="800">
									{title}
								</Heading>
							</Center>
							<div id="yohoho" data-kinopoisk={filmId}></div>
						</>
					)}
				</>
			)}
		</>
	);
}

export default Watch;
